
import Vue from "vue"

export default Vue.extend({
  props: {
    platform: {
      type: String,
      required: true,
      default: "Netflix",
    },
  },
  methods: {
    /**
     * Checks if passed value matches with the prop value.
     * @prop {string} value
     * @returns {boolean}
     */
    isSame(value: string): boolean {
      return this.platform?.toLowerCase() === value?.toLowerCase()
    },
  },
})
