
import Vue from "vue"

/* Interfaces */
interface Platform {
  platform?: string
  classes?: string
}

interface Status {
  component?: string
  title?: string
  classes?: string
}

export default Vue.extend({
  props: {
    rating: {
      type: [String, Number],
      required: true,
      default: "0",
    },
    max: {
      type: [String, Number],
      required: false,
      default: "10",
    },
    isnew: {
      type: Boolean,
      required: false,
      default: false,
    },
    platform: {
      type: String,
      required: false,
      default: null,
    },
  },
  computed: {
    /**
     * Returns platform according to the prop.
     * @returns {Platform}
     */
    getPlatformInfo(): Platform {
      if (!this.platform) return {}

      const platform = this.platform.toLowerCase()
      let classes

      switch (platform) {
        case "netflix":
          classes = "text-red-600 bg-black"
          break
        case "fox":
          classes = "text-gray-100 bg-red-500"
          break
        case "apple tv+":
          classes = "text-white bg-black"
          break
        case "tnt":
          classes = "text-white bg-red-600"
          break
        case "amazon-prime":
          classes = "text-gray-100 bg-blue-500"
          break
        case "disney+":
          classes = "text-white bg-blue-900"
          break
        case "adult-swim":
          classes = "text-gray-100 bg-black"
          break
        case "bbc":
          classes = "text-gray-100 bg-black"
          break
        default:
          classes = "bg-gray-200 dark:bg-neutral-800"
          break
      }

      return {
        platform,
        classes,
      }
    },
  },
})
